import { createForm } from "@formily/core";
import { Schema, ISchema, createSchemaField } from "@formily/react";
import {
  Form,
  FormItem,
  Submit,
  Input,
  Select,
  Radio,
  Checkbox,
  Cascader,
  Switch,
} from "@formily/antd-v5";
import type { FormProps } from "@formily/antd-v5";
import { useMemo } from "react";
import { queryAddress, queryCascader, customJsonp } from "@/service";
import fxr, { XRequest } from "formily-request";
import { notification } from "antd";

const SchemaField = createSchemaField({
  components: {
    FormItem,
    Select,
    Submit,
    Input,
    Radio,
    Switch,
    Checkbox,
    Cascader,
  },
});

type Props = FormProps & {
  onSubmit: (values: unknown) => void;
  schema: ISchema;
};

const $beforeFormat = (format: XRequest["format"]) => {
  return function (res: any) {
    if (res.status !== 0) {
      throw new Error("");
    }
    return typeof format === "function" && format(res);
  };
};

fxr.use(Schema).register({
  baseURL: import.meta.env.VITE_BASIC_API,
  params: {
    key: "L6QBZ-UDFCQ-G6T5R-4D5KA-MV6BV-THFZJ",
  },
  onError(err: any) {
    console.log("invoke onError");
    notification.error({
      message: "",
      description: err.message,
    });
  },
  debug: true,
});

function Demo({ schema, onSubmit, ...formProps }: Props) {
  // schema deps: storybookschema
  const form = useMemo(() => createForm(), [schema]); // eslint-disable-line

  return (
    <div style={{ maxWidth: 600 }}>
      <Form form={form} {...formProps} onAutoSubmit={onSubmit}>
        <SchemaField
          schema={schema}
          scope={{
            queryAddress,
            queryCascader,
            customJsonp,
            $beforeFormat,
          }}
        />
        <div>
          <Submit></Submit>
        </div>
      </Form>
    </div>
  );
}

export default Demo;
